import React from 'react';
import {Button, Intent} from '@blueprintjs/core';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';
import {bindActionCreators} from 'redux';
import {isFormValid} from "../../../../helpers";
import {addPrinter, loadPrinters} from '../../../../actions/PrintersActions';
import Toaster from "../../../Common/Toaster";
import {ADD_PRINTER} from "../../../../helpers/constants";
import _isEqual from "lodash/isEqual";

class New extends React.Component{

    state = {
        serial: '',
        laminator: '',
    };

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadPrinters} = this.props;

        const printer = this.state.serial.toUpperCase();

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadPrinters(token, {});
            
            if(OPERATION_SUCCESSFUL.action === ADD_PRINTER){
                history.goBack();
                Toaster.show({
                    message: `${printer} Successfully Added 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            if(OPERATION_FAILED.action === ADD_PRINTER){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Add ${printer} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState({[name]: value});
    };

    _handleAdd = () => {
        const {authUser: {token}, addPrinter} = this.props;
        const {serial, laminator} = this.state;

        let data = new FormData();

        data.append('serial', serial);
        data.append('laminator', laminator);

        addPrinter(token, data);
    };

    _isFormValid(){
        const {serial, laminator} = this.state;

        let required = {serial, laminator};

        return isFormValid(required);
    }

    render(){
        const {addingPrinter} = this.props;
        const {serial, laminator} = this.state;
        const disableSaveButton = !this._isFormValid();

        return [
            <div key="body" className="bp3-dialog-body">
                <label className="bp3-label">
                    Serial Number
                    <input
                        value={serial}
                        name={'serial'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Laminator Serial Number
                    <input
                        value={laminator}
                        name={'laminator'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div key="footer" className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    <Button
                        disabled={disableSaveButton}
                        loading={addingPrinter}
                        intent={Intent.PRIMARY}
                        onClick={this._handleAdd}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        addPrinter: PropTypes.func.isRequired,
        loadPrinters: PropTypes.func.isRequired,
        addingPrinter: PropTypes.bool.isRequired
    };
}

const mapStateToProps = (
    {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingPrinter}) => (
        {OPERATION_SUCCESSFUL, OPERATION_FAILED, authUser, addingPrinter});

const mapDispatchToProps = dispatch => bindActionCreators({addPrinter, loadPrinters}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(New));


